.skill-card {
    box-shadow: 2px 5px 8px rgba(0, 0, 0, 0.15);
    margin: 0 10px;
	margin-top: 20px;
	padding: 20px 30px;
	display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    border-radius: 10px;
    font-variation-settings: 'wght' 700;
    transition: all ease-in 0.25s;
}
.skill-card:hover{
    cursor: default;
    box-shadow: none;
    transform: translateY(-2px);
    font-variation-settings: 'wght' 200;
}
.skill-text {
    font-family: "Raleway", monospace;
    letter-spacing: 2px;
	font-size: 18px;
	font-weight: 700;
}