.intro {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	min-height: 110vh;
	max-width: 100vw;
	padding:0 10vw;
	background: url("./intro-bg.svg") no-repeat top left;
	background-size: cover;
	background-position-x: center;
	/* background: #f6f6f6; */
}

.intro-name span {
	font-size: 60px;
	font-family: "Montserrat", monospace;
	font-weight: 600;
}	

.intro-name span,.intro-name post{
	color: white;
}

.intro-name post {
	font-size: 24px;
	font-family: "Montserrat", monospace;
	margin-top: 10px;
}

.intro-name {
	width: 60%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.intro-right {
	width: 40%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.intro-guy-image {
	max-width: 100%;
}

.links {
	width: 100%;
	display: flex;
	justify-content: center;
}

a {
	text-decoration: none;
	color: black;
}

.fab {
	font-size: 35px;
}

.links a i {
	cursor: pointer;
	margin: 20px;
}

.index{
	font-weight: 700;
	margin:20px;
	width:100%;
}

ul {
	list-style: none;
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	margin:10px;
}

ul a i{
	margin:0px 2px;
	font-size: 16px;
}

ul a li:hover{
	text-decoration: underline;
}

li{
	font-size: 22px;
	cursor: pointer;
	display: inline;
	font-family: "Montserrat", monospace;
}
@media only screen and (max-width: 820px){
	.intro-guy-image{
		width: 80%;
	}
	.intro-name{
		width: 100%;
	}
	.intro-right{
		width: 100%;
	}
	.intro-name span,.intro-name post{
		color:black;
	}
}
@media only screen and (max-width: 950px){
    .intro{
        padding:0 5vw;
    }
}