@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

html{
    scroll-behavior: smooth;
}
.btn{
    margin:20px;
    width:100px;
    height:50px;
    border:0;
    background:#f6f6f6;
    font-size: 30px;
    font-family: 'Roboto Mono', monospace;
    cursor: pointer;
    font-variation-settings: 'wght' 100;
    transition: all 0.40s ease;
}

.btn:hover{
    font-variation-settings: 'wght' 700;
}
.before-container{
    height: 100vh;
    width: 100vw;
    display: flex;
    background: #f6f6f6;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
h1{
    font-family: 'Roboto Mono',monospace;
    font-variation-settings: 'wght' 700;
}


.intro {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	min-height: 110vh;
	max-width: 100vw;
	padding:0 10vw;
	background: url(/static/media/intro-bg.ad9aaeda.svg) no-repeat top left;
	background-size: cover;
	background-position-x: center;
	/* background: #f6f6f6; */
}

.intro-name span {
	font-size: 60px;
	font-family: "Montserrat", monospace;
	font-weight: 600;
}	

.intro-name span,.intro-name post{
	color: white;
}

.intro-name post {
	font-size: 24px;
	font-family: "Montserrat", monospace;
	margin-top: 10px;
}

.intro-name {
	width: 60%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.intro-right {
	width: 40%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.intro-guy-image {
	max-width: 100%;
}

.links {
	width: 100%;
	display: flex;
	justify-content: center;
}

a {
	text-decoration: none;
	color: black;
}

.fab {
	font-size: 35px;
}

.links a i {
	cursor: pointer;
	margin: 20px;
}

.index{
	font-weight: 700;
	margin:20px;
	width:100%;
}

ul {
	list-style: none;
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	margin:10px;
}

ul a i{
	margin:0px 2px;
	font-size: 16px;
}

ul a li:hover{
	text-decoration: underline;
}

li{
	font-size: 22px;
	cursor: pointer;
	display: inline;
	font-family: "Montserrat", monospace;
}
@media only screen and (max-width: 820px){
	.intro-guy-image{
		width: 80%;
	}
	.intro-name{
		width: 100%;
	}
	.intro-right{
		width: 100%;
	}
	.intro-name span,.intro-name post{
		color:black;
	}
}
@media only screen and (max-width: 950px){
    .intro{
        padding:0 5vw;
    }
}
.about {
    max-width:100vw;
    padding:40px 10vw;
}
.heading{
    font-family: 'Raleway', sans-serif;
    font-size: 32px;
    font-weight: 500;
    letter-spacing: 7px;
}
.about-inner-text .text{
    margin:10px 0;
    text-align: justify;
    font-size: 16px;
    line-height: 200%;
    font-family: 'Montserrat',monospace;
}
.about-inner{
    margin-top: 20px;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.15);
    padding:20px 0;
    display: flex;
    flex-wrap: wrap;
    transition: 0.35s ease;
    border-radius: 10px;
}
.about-inner:hover{
    box-shadow: 2px 5px 20px rgba(0, 0, 0, 0.15);
    transform: translateY(-5px);
}
.about-image{
    width:120px;
    height:120px;
    border-radius: 50%;
}
.about-inner-image{
    display: flex;
    align-items: center;
    justify-content: center;
    width:20%;
    height: auto;
}
.about-inner-text{
    padding:20px;
    padding-right: 40px;
    width:80%;
}
@media only screen and (max-width: 820px){
    .about-inner-image{
        width:100%
    }
    .about-inner-text{
        width: 100%;
    }

}
