#project .heading{
    color: white;
    
}
.project-inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding:20px 0;
}

.project-card {
	box-shadow: 0px 20px 60px rgba(54, 49, 49, 0.15);
	width: 300px;
	border-radius: 10px;
	overflow: hidden;
	align-self: center;
	margin: 20px;
	width: 40%;
}
.project-card:hover .project-text-container {
	padding: 20px 10%;
	max-height: 500px;
}
.project-card:hover .project-header {
	opacity: 1;
}
.project-card:hover .project-description {
	opacity: 1;
}
@media only screen and (max-width: 680px){
	.project-card{
		width:100%;
	}
}
.project-image-container{
    margin:0;
    padding:0;
}
.project-image{
    width:100%;
}
.project-text-container {
	transition: 1s linear;
	padding: 0px 10%;
	max-height: 0;
}
.project-title {
	font-family: "Montserrat", monospace;
}

.project-description,.project-header {
	cursor: default;
	opacity: 0;
	transition: all 0.5s;
	transition-delay: 1s;
}
.project-description {
	font-family: "Raleway";
	line-height: 150%;
	text-align: left;
	margin: 20px 0;
}
.project-technologies {
	font-family: "Raleway";
	line-height: 150%;
	text-align: left;
	margin: 20px 0;
}
.project-header {
    display: flex;
    justify-content: space-between;
}
.project-header a i{
    margin-top: 4px;
    font-size: 16px;
}
.skill-card {
    box-shadow: 2px 5px 8px rgba(0, 0, 0, 0.15);
    margin: 0 10px;
	margin-top: 20px;
	padding: 20px 30px;
	display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    border-radius: 10px;
    font-variation-settings: 'wght' 700;
    transition: all ease-in 0.25s;
}
.skill-card:hover{
    cursor: default;
    box-shadow: none;
    transform: translateY(-2px);
    font-variation-settings: 'wght' 200;
}
.skill-text {
    font-family: "Raleway", monospace;
    letter-spacing: 2px;
	font-size: 18px;
	font-weight: 700;
}
#skills{
    max-width:100vw;
    padding:40px 10vw;
    display: flex;
    flex-direction: column;
}
.skills-inner{
    display: flex;
    flex-wrap: wrap;
}
.skills-inner{
    display: flex;
}
#footer {
    max-width:100vw;
    padding:40px 10vw;
    display: flex;
    flex-direction: column;
    padding-bottom: 0;
    background-color: #007fd4;
