.about {
    max-width:100vw;
    padding:40px 10vw;
}
.heading{
    font-family: 'Raleway', sans-serif;
    font-size: 32px;
    font-weight: 500;
    letter-spacing: 7px;
}
.about-inner-text .text{
    margin:10px 0;
    text-align: justify;
    font-size: 16px;
    line-height: 200%;
    font-family: 'Montserrat',monospace;
}
.about-inner{
    margin-top: 20px;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.15);
    padding:20px 0;
    display: flex;
    flex-wrap: wrap;
    transition: 0.35s ease;
    border-radius: 10px;
}
.about-inner:hover{
    box-shadow: 2px 5px 20px rgba(0, 0, 0, 0.15);
    transform: translateY(-5px);
}
.about-image{
    width:120px;
    height:120px;
    border-radius: 50%;
}
.about-inner-image{
    display: flex;
    align-items: center;
    justify-content: center;
    width:20%;
    height: auto;
}
.about-inner-text{
    padding:20px;
    padding-right: 40px;
    width:80%;
}
@media only screen and (max-width: 820px){
    .about-inner-image{
        width:100%
    }
    .about-inner-text{
        width: 100%;
    }

}