.project-text-container {
	transition: 1s linear;
	padding: 0px 10%;
	max-height: 0;
}
.project-title {
	font-family: "Montserrat", monospace;
}

.project-description,.project-header {
	cursor: default;
	opacity: 0;
	transition: all 0.5s;
	transition-delay: 1s;
}
.project-description {
	font-family: "Raleway";
	line-height: 150%;
	text-align: left;
	margin: 20px 0;
}
.project-technologies {
	font-family: "Raleway";
	line-height: 150%;
	text-align: left;
	margin: 20px 0;
}
.project-header {
    display: flex;
    justify-content: space-between;
}
.project-header a i{
    margin-top: 4px;
    font-size: 16px;
}