.project-card {
	box-shadow: 0px 20px 60px rgba(54, 49, 49, 0.15);
	width: 300px;
	border-radius: 10px;
	overflow: hidden;
	align-self: center;
	margin: 20px;
	width: 40%;
}
.project-card:hover .project-text-container {
	padding: 20px 10%;
	max-height: 500px;
}
.project-card:hover .project-header {
	opacity: 1;
}
.project-card:hover .project-description {
	opacity: 1;
}
@media only screen and (max-width: 680px){
	.project-card{
		width:100%;
	}
}