.btn{
    margin:20px;
    width:100px;
    height:50px;
    border:0;
    background:#f6f6f6;
    font-size: 30px;
    font-family: 'Roboto Mono', monospace;
    cursor: pointer;
    font-variation-settings: 'wght' 100;
    transition: all 0.40s ease;
}

.btn:hover{
    font-variation-settings: 'wght' 700;
}